export const fetchVerses = async (searchTerm = '') => {
    try {
        let response = await fetch(`https://bible-api.com/${searchTerm}?translation=almeida`);
        
        if (response.ok) {
            const data = await response.json();
            return data;
        } else {
            // Segunda tentativa sem a traduo "almeida"
            console.warn("Traduo 'Almeida' no encontrada");
            response = await fetch(`https://bible-api.com/${searchTerm}`);
            
            if (response.ok) {
                const data = await response.json();
                return data;
            } else {
                console.error('Erro ao buscar versculo sem traduo:');
                return [];
            }
        }
    } catch (error) {
        console.error('Erro ao buscar versculos:', error);
        return [];
    }
};
